#board{
  height:400px;
  width: 300px;
  margin: 10px auto;
  padding: 30px;
  outline: "none";
}

td{
  border: 2px solid rgb(34, 15, 15);
  cursor: "pointer";
  height: 33.33%;
  width: 33.33%;
}
td:hover,button{
  cursor: pointer;
  background:#eee;
}
hr{
  color: red;
  border-bottom: 2px solid black;
}